import React, { useEffect, useRef } from "react";
import logo from "./logo.svg";
import "./App.css";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
} from "react-router-dom";

const App = () => {
  const wordArray = useRef([]);
  const currentWord = useRef(0);
  const wordsRef = useRef([]);

  const splitLetters = (word) => {
    const content = word.innerHTML;
    word.innerHTML = "";
    const letters = [];
    for (let i = 0; i < content.length; i++) {
      const letter = document.createElement("span");
      letter.className = "letter";
      letter.innerHTML = content.charAt(i);
      word.appendChild(letter);
      letters.push(letter);
    }

    wordArray.current.push(letters);
  };

  const animateLetterOut = (cw, i) => {
    setTimeout(() => {
      cw[i].className = "letter out";
    }, i * 80);
  };

  const animateLetterIn = (nw, i) => {
    setTimeout(() => {
      nw[i].className = "letter in";
    }, 340 + i * 80);
  };

  const changeWord = () => {
    const cw = wordArray.current[currentWord.current];
    const nw =
      currentWord.current === wordsRef.current.length - 1
        ? wordArray.current[0]
        : wordArray.current[currentWord.current + 1];

    for (let i = 0; i < cw.length; i++) {
      animateLetterOut(cw, i);
    }

    for (let i = 0; i < nw.length; i++) {
      nw[i].className = "letter behind";
      nw[0].parentElement.style.opacity = 1;
      animateLetterIn(nw, i);
    }

    currentWord.current =
      currentWord.current === wordArray.current.length - 1
        ? 0
        : currentWord.current + 1;
  };

  useEffect(() => {
    wordsRef.current = document.getElementsByClassName("word");

    wordsRef.current[currentWord.current].style.opacity = 1;
    for (let i = 0; i < wordsRef.current.length; i++) {
      splitLetters(wordsRef.current[i]);
    }

    changeWord();
    const interval = setInterval(changeWord, 2000);

    return () => clearInterval(interval);
  }, []);

  return (
    <div class="text">
      <p>Stars are &nbsp;</p>
      <p>
        <span class="word wisteria">Inspirational.</span>
        <span class="word belize">Mysterious.</span>
        <span class="word pomegranate">Enduring.</span>
        <span class="word green">Illuminating.</span>
        <span class="word midnight">Majestic.</span>
      </p>
    </div>
  );
};

export default App;



